@import 'reset/reset.css';
html {
  font-family: 'Roboto';
  font-weight: 300;
  font-size: 24px;
  color: #fff;
  height: auto;
  padding: 30px;
}
div div {
  lost-utility: clearfix;
  height: 100px;
  line-height: 100px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 30px;
}
div div div {
  margin-top: 0;
  margin-bottom: 0;
  lost-utility: clearfix;
}
section {
  lost-center: 1140px;
  lost-utility: clearfix;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 30px 30px 0;
}
section h1 {
  margin: 0 0 30px 0;
}
section:last-of-type {
  margin-bottom: 0;
}
section:nth-of-type(1) {
  background: #42cafd;
}
section:nth-of-type(1) div div {
  lost-column: 1/1;
  background: #22c1fd;
}
@media (min-width: 500px) {
  section:nth-of-type(1) div div {
    lost-column: 1/2;
  }
}
@media (min-width: 800px) {
  section:nth-of-type(1) div div {
    lost-column: 1/3;
  }
}
@media (min-width: 1100px) {
  section:nth-of-type(1) div div {
    lost-column: 1/4;
  }
}
section:nth-of-type(2) {
  background: #66b3ba;
}
section:nth-of-type(2) div div {
  lost-column: 1/3;
  background: #48989f;
}
section:nth-of-type(2) div div:first-child {
  lost-offset: -1/3;
}
section:nth-of-type(3) {
  background: #8eb19d;
}
section:nth-of-type(3) div div {
  lost-column: 1/1;
  background: #68977c;
}
@media (min-width: 500px) {
  section:nth-of-type(3) div div {
    lost-column: 1/3;
  }
}
@media (max-width: 499px) {
  section:nth-of-type(3) div div:last-child {
    float: none;
  }
}
section:nth-of-type(3) div div div {
  background: #557b65;
  lost-column: 1/1;
  margin-bottom: 30px;
}
@media (min-width: 500px) {
  section:nth-of-type(3) div div div {
    lost-column: 1/2;
  }
}
section:nth-of-type(4) {
  background: #f0d2d1;
  height: 400px;
}
section:nth-of-type(4) div {
  lost-align: center;
  background: #dfa09e;
  height: 285px;
  float: none;
}
section:nth-of-type(4) div div {
  background: #cf6f6b;
  lost-column: 1/3;
  height: 100px;
}
section:nth-of-type(5) {
  background: #f19a3e;
}
section:nth-of-type(5) div div {
  lost-column: 1/1;
  background: #ef8b22;
  padding: 15px;
  height: auto;
}
@media (min-width: 500px) {
  section:nth-of-type(5) div div {
    lost-column: 2/4 2;
  }
}
@media (min-width: 800px) {
  section:nth-of-type(5) div div {
    lost-column: 2/6 3;
  }
}
@media (min-width: 1100px) {
  section:nth-of-type(5) div div {
    lost-column: 2/8 4;
  }
}
section:nth-of-type(5) div p {
  line-height: 25px;
  font-size: 16px;
}
section:nth-of-type(6) {
  background: #1e90ff;
}
section:nth-of-type(6) div {
  margin-bottom: 30px;
}
section:nth-of-type(6) div div {
  lost-row: 1/3;
  background: #007bf2;
}
section:nth-of-type(7) {
  background: #ff6347;
}
section:nth-of-type(7) div {
  margin-bottom: 30px;
  lost-utility: clearfix;
}
section:nth-of-type(7) div div {
  lost-waffle: 1/3;
  background: #ff3a16;
}
section:nth-of-type(8) {
  background: #5f9ea0;
}
section:nth-of-type(8) div div {
  background: #4c7e80;
}
section:nth-of-type(8) div div:first-child {
  lost-column: 1/3;
}
section:nth-of-type(8) div div:last-child {
  lost-column: 2/3;
}
section:nth-of-type(9) {
  background: #00ff7f;
}
section:nth-of-type(9) div div {
  background: #00b359;
  lost-column: 1/2;
}
section:nth-of-type(9) div div:first-child {
  lost-move: 1/2;
}
section:nth-of-type(9) div div:last-child {
  lost-move: -1/2;
}
